.fancybox-close {
  position: fixed;
  right: 30px;
  top: 20px;
  width: 24px;
  height: 24px;
  z-index: 8041;
  background: center center no-repeat url("../../assets/img/close.svg");
  background-size: 24px;
  opacity: .6;
  transition: all .2s;
  margin-right: 0 !important;
}
.fancybox-close:hover,
.fancybox-close:active,
.fancybox-close:focus {
  opacity: 1;
}
.fancybox-skin {
  color: #161f6a;
  border-radius: 0;
  box-shadow: none !important;
  background-color: transparent;
}
.fancybox-nav {
  width: 150px;
}
.fancybox-nav:hover span {
  color: #fff;
  opacity: 1;
}
.fancybox-nav span {
  position: sticky;
  display: block;
  visibility: visible;
  opacity: .5;
  margin-top: -17px;
  top: 355px;
  width: 55px;
  height: 34px;
  transition: opacity .2s;
}
.fancybox-prev {
  left: -150px;
}
.fancybox-prev span {
  left: 0;
  transform: translateX(50px);
  background: left top no-repeat url("../img/slider-arrow.png");
  background-size: 55px 34px;
}
.fancybox-next {
  right: -150px;
}
.fancybox-next span {
  right: 0;
  background: left top no-repeat url("../img/slider-arrow.png");
  background-size: 55px 34px;
  transform: rotate(180deg) translateX(-45px);
}
.fancybox-type-image {
  max-width: calc(100vw - 220px);
}
.fancybox-type-image .fancybox-inner {
  max-width: calc(100vw - 220px);
}
.fancybox-type-image .fancybox-nav span {
  top: 50%;
}
.alert-block {
  text-align: left;
}
