.fancybox-inner {
  overflow: hidden !important;
  height: auto !important;
}
.fancybox-inner .fancybox-container {
  width: 100vw !important;
  padding: 80px 0 0;
  background: #fff;
  border-radius: 15px 15px 0 0;
}
.fancybox-overlay {
  top: 64px;
  background: transparent;
  border-radius: 15px 15px 0 0;
  height: calc(100vh - 64px);
  overflow: hidden !important;
}
.fancybox-overlay::-webkit-scrollbar {
  background: transparent;
  width: 0;
}
.fancybox-overlay::-webkit-scrollbar-thumb {
  background: #999 !important;
  border-radius: 5px;
}
.fancybox-overlay .fancybox-container {
  width: 100vw !important;
}
.fancybox-wrap {
  height: calc(100vh - 64px) !important;
  overflow: scroll !important;
  overflow-x: hidden !important;
}
.fancybox-wrap::-webkit-scrollbar {
  background: transparent;
  width: 0;
}
.fancybox-overlay_bg_white {
  background: #fff;
  overflow: auto !important;
  overflow-x: hidden !important;
}
.fancybox-overlay_bg_white .fancybox-container {
  width: 100vw !important;
}
.b-fancybox-overlay {
  background: rgba(22, 31, 106, 0.8);
  cursor: pointer;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all ease-in-out 0.2s;
  z-index: 8009;
}
.b-fancybox-overlay:hover,
.b-fancybox-overlay:active,
.b-fancybox-overlay:focus {
  background: rgba(22, 31, 106, 0.6);
}
.b-fancybox-overlay:hover .fancybox-close,
.b-fancybox-overlay:active .fancybox-close,
.b-fancybox-overlay:focus .fancybox-close {
  opacity: 1;
}
.b-fancybox-nav {
  position: absolute;
  top: 0;
  height: 100%;
  width: 150px;
  cursor: pointer;
}
.b-fancybox-nav:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 102px;
  height: 102px;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border: 1px solid #EBEFFE;
  transition: all ease-in-out 0.2s;
}
.b-fancybox-nav:hover:before,
.b-fancybox-nav:active:before,
.b-fancybox-nav:focus:before {
  border: 1px solid #2856F6;
}
.b-fancybox-nav.b-fancybox-prev {
  left: -150px;
  background: center center no-repeat url("../../assets/img/arrow-left.svg");
  background-size: 53px 26px;
}
.b-fancybox-nav.b-fancybox-next {
  right: -150px;
  background: center center no-repeat url("../../assets/img/arrow-right.svg");
  background-size: 53px 26px;
}
.fancybox-nav {
  display: none;
}
